.card {
  background: lightgray;
  border-radius: 2px;
  margin: 1rem;
  position: relative;
  text-align: center;
}

.card > .img-container {
  height: 60%;
  overflow: hidden;
  text-align: center;
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}

.card > .content > ul {
  list-style-type: none;
}

.card > .content > ul > li {
  margin-top: 18px;
}

.card > .remove {
  color: #ff1744;
  font-size: 48px;
  position: absolute;
  top: -20px;
  right: 5px;
  cursor: pointer;
}

.card > .remove:hover {
  color: #d50000;
}

li {
  display: inline-block;
}